import mongoose, { Schema, Document } from 'mongoose';

// ุชุนุฑูู ูุงุฌูุฉ ุงูุฑุณุงูุฉ
export interface IChatMessage extends Document {
  sender: string;
  senderAvatarId: number;
  message: string;
  timestamp: string;
  interaction: {
    likes: string[];
    isLiked: boolean;
  };
}

// ุชุนุฑูู ุณูููุง ุงูุฑุณุงูุฉ
const ChatMessageSchema: Schema = new Schema({
  sender: {
    type: String,
    required: true,
    trim: true
  },
  senderAvatarId: {
    type: Number,
    required: true,
    default: 1
  },
  message: {
    type: String,
    required: true,
    trim: true
  },
  timestamp: {
    type: String,
    required: true,
    default: () => new Date().toISOString()
  },
  interaction: {
    likes: {
      type: [String],
      default: []
    },
    isLiked: {
      type: Boolean,
      default: false
    }
  }
}, {
  timestamps: true,
  versionKey: false
});

// ุชุนุฑูู ุงููููุฐุฌ
let ChatMessageModel: mongoose.Model<IChatMessage>;

try {
  // ูุญุงููุฉ ุงูุญุตูู ุนูู ุงููููุฐุฌ ุฅุฐุง ูุงู ููุฌูุฏุงู ุจุงููุนู
  ChatMessageModel = mongoose.model<IChatMessage>('ChatMessage');
} catch {
  // ุฅูุดุงุก ุงููููุฐุฌ ุฅุฐุง ูู ููู ููุฌูุฏุงู
  ChatMessageModel = mongoose.model<IChatMessage>('ChatMessage', ChatMessageSchema);
}

export default ChatMessageModel; 