import { NextRequest, NextResponse } from 'next/server';
import dbConnect from '@/lib/dbConnect';
import ChatMessageModel from '@/models/ChatMessage';
import { Model } from 'mongoose';

// ุงูุญุตูู ุนูู ุงูุฑุณุงุฆู ูู ูุงุนุฏุฉ ุงูุจูุงูุงุช
export async function GET(req: NextRequest) {
  try {
    await dbConnect();
    
    const url = new URL(req.url);
    const limit = parseInt(url.searchParams.get('limit') || '50');
    
    // ุงุณุชุฑุฌุงุน ุงูุฑุณุงุฆู ูู ูุงุนุฏุฉ ุงูุจูุงูุงุช
    const messages = await (ChatMessageModel as Model<any>)
      .find({})
      .sort({ timestamp: -1 })
      .limit(limit)
      .lean();
    
    // ุชุฑุชูุจ ุงูุฑุณุงุฆู ูู ุงูุฃูุฏู ุฅูู ุงูุฃุญุฏุซ ููุนุฑุถ
    const sortedMessages = [...messages].sort((a, b) => 
      new Date(a.timestamp).getTime() - new Date(b.timestamp).getTime()
    );
    
    return NextResponse.json({ 
      success: true, 
      messages: sortedMessages
    });
  } catch (error) {
    console.error('Error fetching chat messages:', error);
    return NextResponse.json({ 
      success: false, 
      error: 'ูุดู ูู ุฌูุจ ุฑุณุงุฆู ุงูุฏุฑุฏุดุฉ',
      message: (error as Error).message
    }, { status: 500 });
  }
}

export async function POST(req: NextRequest) {
  try {
    await dbConnect();
    
    const { message, sender, senderAvatarId } = await req.json();
    
    // ุงูุชุญูู ูู ุจูุงูุงุช ุงูุฑุณุงูุฉ
    if (!message || typeof message !== 'string' || !message.trim()) {
      return NextResponse.json({ 
        success: false, 
        error: 'ูุญุชูู ุงูุฑุณุงูุฉ ูุทููุจ' 
      }, { status: 400 });
    }
    
    if (!sender || typeof sender !== 'string') {
      return NextResponse.json({ 
        success: false, 
        error: 'ุงุณู ุงููุฑุณู ูุทููุจ' 
      }, { status: 400 });
    }
    
    // ุฅูุดุงุก ุฑุณุงูุฉ ุฌุฏูุฏุฉ
    const newMessage = {
      sender,
      senderAvatarId: senderAvatarId || 1,
      message: message.trim(),
      timestamp: new Date().toISOString(),
      interaction: {
        likes: [],
        isLiked: false
      }
    };
    
    // ุญูุธ ุงูุฑุณุงูุฉ ูู ูุงุนุฏุฉ ุงูุจูุงูุงุช
    const savedMessage = await (ChatMessageModel as Model<any>).create(newMessage);
    
    return NextResponse.json({ 
      success: true, 
      message: savedMessage
    });
  } catch (error) {
    console.error('Error sending chat message:', error);
    return NextResponse.json({ 
      success: false, 
      error: 'ูุดู ูู ุฅุฑุณุงู ุฑุณุงูุฉ ุงูุฏุฑุฏุดุฉ',
      message: (error as Error).message
    }, { status: 500 });
  }
} 